import Geometry from './geometry'
import Polygon from './polygon'
import { 
  getProjectivePoint,
  getPointInExtendedLineByDistanceFromAB,
  distance
} from '../geometry/support/geometryutil'
import {ExtentUtil} from './support/extentutil'
import Extent from './extent';
import Log from '../utils/log';

export default class RotatableExtent extends Polygon {

  constructor(coordinates) {
    super(coordinates)

    this.rotable = true
    this._anchorSide = null
    this._width = 0
    this._height = 0
  }

  get geometryType() { return Geometry.ROTATABLE_EXTENT }

  get width() { return this._width }

  get height() { return this._height }

  setCoordinates(coords) {
    super.setCoordinates(coords)

    if(coords.length === 0) {
      return
    }
    
    const coordinates = coords[0]

    if (coordinates.length < 3) {
      return
    }

    const [p1, p2, p3] = coordinates
    const d1 = distance(p1, p2)
    const d2 = distance(p2, p3)

    if (Number.isNaN(d1) || Number.isNaN(d2)) {
      return
    }

    if (d1 > d2) {
      this._height = d2
      this._width = d1
    } else {
      this._height = d1
      this._width = d2
    }
  }

  /**
   * 
   * @param {Number} angle 
   * @param {Array} anchor 
   */
  rotate(angle, anchor) {
    // 
    if (this._anchor) {
      const [x, y] = this._anchor
      this._anchor = super.rotateXY(x, y, anchor, angle)
    }

    // 
    super.rotate(angle, anchor)
  }

  /**
   * 
   */
  _findAnchorEdge() {
    let coordinates = this.getCoordinates()
    coordinates = coordinates[0]

    // 
    const [p1, p2, p3] = coordinates

    const d1 = distance(p1, p2)
    const d2 = distance(p2, p3)

    let [idx1, idx2] = [0, 0]

    // 
    if (d1 > d2) {
      idx1 = 0
      idx2 = 1
    } else {
      idx1 = 1
      idx2 = 2
    }

    const [idx3, idx4] = [idx1 + 2, idx2 + 2]

    const [c1, c2, c3, c4] = [
      coordinates[idx1], coordinates[idx2],
      coordinates[idx3], coordinates[idx4]
    ]

    // 
    const center1 = [(c1[0] + c2[0]) / 2, (c1[1] + c2[1]) / 2]
    const center2 = [(c3[0] + c4[0]) / 2, (c3[1] + c4[1]) / 2]

    let [uponEdge, belowEdge] = [null, null]

    // 
    if (center1[1] > center2[1]) {
      uponEdge = [idx1, idx2]
      belowEdge = [idx3, idx4]
    } else {
      uponEdge = [idx3, idx4]
      belowEdge = [idx1, idx2]
    }

    return { uponEdge, belowEdge }
  }

  /**
   * 
   */
  getAnchorForRotating(map) {
    if (this._anchorSide === null) {
      this._anchorSide = this._findAnchorEdge()
    }

    const { uponEdge, belowEdge } = this._anchorSide

    let coordinates = this.getCoordinates()
    coordinates = coordinates[0]

    const [c1, c2, c3, c4] = [
      coordinates[uponEdge[0]], coordinates[uponEdge[1]], 
      coordinates[belowEdge[0]], coordinates[belowEdge[1]]
    ]

    // 
    const center1 = [(c1[0] + c2[0]) / 2, (c1[1] + c2[1]) / 2]
    const center2 = [(c3[0] + c4[0]) / 2, (c3[1] + c4[1]) / 2]

    // 
    let xa = center1[0]
    let ya = center1[1]

    let xb = center2[0]
    let yb = center2[1]

    const resolution = map.view.resolution

    // AB
    const anchor = getPointInExtendedLineByDistanceFromAB(
      xa, ya, xb, yb, 25 * resolution)

    this._anchor = anchor
    return this._anchor
  }

  clone() {
    return new RotatableExtent(this.getCoordinates())
  }

  /**
   * 
   * @param coordinates
   * @returns {Array}
   */
  static getTheLastTwoPoint(coordinates) {
    let [pointA, pointB, pointC] = coordinates
    let [pointD, pointE] = [null, null]

    // 0
    if (pointA[1] === pointB[1]) {
      pointD = [pointB[0], pointC[1]]
      pointE = [pointA[0], pointC[1]]

      return [pointD, pointE]
    }

    if (pointA[0] === pointB[0]) {
      pointD = [pointC[0], pointB[1]]
      pointE = [pointC[0], pointA[1]]

      return [pointD, pointE]
    }

    const kAB = (pointA[1] - pointB[1]) / (pointA[0] - pointB[0])
    const k = -1 / kAB

    // 
    pointD = getProjectivePoint(pointB, k, pointC)
    const dBD = distance(pointD, pointB)

    const [x2, y2] = pointA
    let [x1, y1] = [0, 0]

    // 
    const dx = Math.sqrt(dBD * dBD / (k * k + 1))
    const dy = k * Math.sqrt(dBD * dBD / (k * k + 1))

    x1 = x2 - dx
    y1 = y2 - dy

    const dk = (pointD[1] - y1) / (pointD[0] - x1)

    // 
    if (dk.toFixed(2) != kAB.toFixed(2)) {
      x1 = x2 + dx
      y1 = y2 + dy
    }

    pointE = [x1, y1]

    return [pointD, pointE]
  }

  static handleRect(oldCoordinates, dragSegment, vertex) {
    const [p1, p2, p3, p4] = oldCoordinates
    if (p1[0] === p4[0] &&
        p2[0] === p3[0] &&
        p1[1] === p2[1] && 
        p3[1] === p4[1]) {

      console.log('')


      let xmin = Number.POSITIVE_INFINITY
      let ymin = Number.POSITIVE_INFINITY
      let xmax = Number.NEGATIVE_INFINITY
      let ymax = Number.NEGATIVE_INFINITY
    
      oldCoordinates.forEach(ring => {
        xmin = Math.min(xmin, ring[0])
        ymin = Math.min(ymin, ring[1])
        xmax = Math.max(xmax, ring[0])
        ymax = Math.max(ymax, ring[1])
      })

      const coords = [
        [xmin, ymin], 
        [xmax, ymin],
        [xmax, ymax],
        [xmin, ymax],
        [xmin, ymin]
      ]

      const newCoordinates = ExtentUtil.updateExtent(
        { 
          getCoordinates: function () { return coords}
        }, vertex, dragSegment)
      // Log.coordinates(newCoordinates)
      return newCoordinates
    }

    return null
  }

  /**
   * 
   *
   * @param p 
   * @param vertex 
   * @param oldVertex 
   * @param dragSegment 
   * @returns {*} 3
   */
  static updateCoordinatesForModification(p, vertex, oldVertex, dragSegment) {
    let dis = 0
    if (oldVertex) {
      dis = distance(oldVertex, vertex)
    }

    const oldRingsCoordinates = p.getCoordinates()
    if (dis === 0) {
      return oldRingsCoordinates
    }

    const oldCoordinates = [...oldRingsCoordinates[0]]
    const segement = dragSegment[0]
    const edgeIndex = segement.index

    // const rectCoordinates = RotatableExtent.handleRect(
    //   oldCoordinates, dragSegment, vertex)

    // if (rectCoordinates) {
    //   return rectCoordinates
    // }

    let firstCoord = null
    let nowCood = null
    let otherCood = null

    // 1
    if (edgeIndex === 0 || edgeIndex === 4) {
      firstCoord = oldCoordinates[3]
      nowCood = oldCoordinates[0]
      otherCood = oldCoordinates[1]
    } else {
      firstCoord = oldCoordinates[edgeIndex - 1]
      nowCood = oldCoordinates[edgeIndex]
      otherCood = oldCoordinates[edgeIndex + 1]
    }

    // 
    const newCood1 = getPointInExtendedLineByDistanceFromAB(firstCoord[0],
      firstCoord[1], nowCood[0], nowCood[1], dis)

    // 
    const tempXX = nowCood[0] - firstCoord[0]
    const tempYY = nowCood[1] - firstCoord[1]
    const tempX2 = vertex[0] - oldVertex[0]
    const tempY2 = vertex[1] - oldVertex[1]

    // 
    //  <0 
    const direction = (tempX2 * tempXX) + (tempY2 * tempYY)

    
    const dx = newCood1[0] - nowCood[0]
    const dy = newCood1[1] - nowCood[1]
    let newCood2
    
    // 
    if (direction < 0) {
      newCood1[0] = nowCood[0] - dx
      newCood1[1] = nowCood[1] - dy

      newCood2 = [otherCood[0] - dx, otherCood[1] - dy]
    } else {
      newCood2 = [otherCood[0] + dx, otherCood[1] + dy]
    }

    oldCoordinates[edgeIndex] = newCood1
    oldCoordinates[edgeIndex + 1] = newCood2

    if (edgeIndex === 0) {
      oldCoordinates[4] = oldCoordinates[0].slice()
    } else if (edgeIndex === 4 || edgeIndex === 3) {
      oldCoordinates[0] = oldCoordinates[4].slice()
    }

    return [oldCoordinates]
  }

}


