import Component from './component'
import BrowserEvent from '../meek/browserevent'
import Geometry from '../geometry/geometry'
import Point from '../geometry/point'
import Line from '../geometry/line'
import Parallelogram from '../geometry/parallelogram'
import RotatableExtent from '../geometry/rotatableextent'
import ModifyEvent from './modifyevent'
import Feature from '../meek/feature'
import FeatureEvent from '../meek/featureevent'
import FeatureLayer from '../lyr/featurelayer'
import LineStyle from '../style/linestyle'
import PointStyle from '../style/pointstyle'

import { Style } from '../style/style'
import { EventType } from '../meek/eventtype'
import { ExtentUtil } from '../geometry/support/extentutil'
import { listen, unlistenByKey } from '../core/eventmanager'
import {
  closestOnSegment, 
  squaredDistanceToSegment,
  squaredDistance, 
  distance,
  getAngleBetweenTwoVectors
} from '../geometry/support/geometryutil'

import {
  getCollisionFeaturesByPoint
} from '../utils/feature'

import { noModifierKeys, singleClick } from '../utils/mousekey'
import { functions } from '../utils/functions'


/**
 * 
 *
 * @class Modify
 * @extends Component
 * @module component
 * @constructor
 * @example
 *
 *      // 
 *      var modifyTool = new mk.Modify({
 *        features: selectedFeaturs
 *      })
 */
export default class Modify extends Component {

  /**
   * 
   *
   * @constructor constructor
   * @param options
   */
  constructor(options = {}) {
    super(options)
    
    this.applyHandleEventOption({
      handleDownEvent: this._handleDownEvent,
      handleDragEvent: this._handleDragEvent,
      handleUpEvent: this._handleUpEvent
    })
  
    /**
     * feature
     *
     * @property features
     * @type {Array}
     * @private
     */
    this._features = []
    
    /**
     * 
     *
     * @property pixelTolerance
     * @type {number}
     * @private
     */
    this._pixelTolerance = options.pixelTolerance ?
        options.pixelTolerance : 8
  
    /**
     * 
     *
     * @property movableGeometrys
     * @type {[*]}
     * @private
     */
    this._movableGeometrys = options.movableGeometrys ?
        options.movableGeometrys : 
        [ Geometry.EXTENT, Geometry.ROTATABLE_EXTENT, Geometry.POINT ]
  
    /**
     * 
     * hasMoveableGeometrys
     * @type {boolean}
     * @private
     */
    this._hasMoveableGeometrys = true
    
    const movableGeometrys = this._movableGeometrys
    if (movableGeometrys === null || movableGeometrys === undefined) {
      this._hasMoveableGeometrys = false
    }
  
    if (Array.isArray(movableGeometrys)) {
      if (movableGeometrys.length === 0) {
        this._hasMoveableGeometrys = false
      }
    }
  
    /**
     *
     * @type {Set}
     * @private
     */
    this.movableGeometrySet
    
    if (this._hasMoveableGeometrys) {
      this.movableGeometrySet = new Set(this._movableGeometrys)
    }
  
    /**
     * @private
     */
    this._deleteCondition = options.deleteCondition ?
      options.deleteCondition : this._defaultDeleteCondition
  
    /**
     * 
     * @private
     */
    this._vertexEditableCondition = options.vertexEditableCondition ?
      options.vertexEditableCondition : functions.TURE
  
    /**
     * 
     * @private
     */
    this._edgeEditableCondition = options.edgeEditableCondition ?
      options.edgeEditableCondition : functions.TURE
    
    /**
     * Determine if should snap to vertexs of gemetry
     *
     * snappedToVertex
     * @type {boolean}
     * @private
     */
    this._snappedToVertex = false

    // snap
    const snapOptions = options.snapOptions || {}

    /**
     * Detemene if should snap to other geometry
     * @type {boolean}
     * @provate
     */
    this._snapToOthers = snapOptions.snapToOthers ? 
      snapOptions.snapToOthers : false

    /**
     * features
     */
    this._featureLayer = snapOptions.featureLayer ? 
      snapOptions.featureLayer : null
  
    /**
     * 
     * @snapSegments
     * @type {null}
     * @private
     */
    this._snapSegments = null
  
    /**
     * @vertexFeature
     * @type {null}
     * @private
     */
    this._vertexFeature = null

    /**
     * 
     */
    this._anchorFeature = null

    /**
     * 
     */
    this._anchorPointStyle = new PointStyle(
      14, [255, 255, 255], 0.9, new LineStyle([255, 0, 0], 1, 2))

    /**
     * 
     */
    this._rotatableFeature = null

    /**
     * 
     */
    this._rotatingFeature = null

    /**
     * -
     */
    this._rotatableLine = null

    /**
     * 
     */
    this._rotatableLineStyle = new LineStyle([255, 0, 0], 1, 1)

    /**
     * 
     */
    this._lastRotatablePoint = null
  
    /**
     * The editable features
     *
     * 
     * 
     *
     * @property features
     * @type {[]}
     */
    this.features = options.features || []
  
    /**
     *
     * @type {[*]}
     * @private
     */
    this._lastPixel = [0, 0]
  
    /**
     * 
     *
     * @property overLayer
     * @type {FeatureLayer}
     * @private
     */
    this._overLayer = new FeatureLayer({
      style: options.style ? options.style : this.getDefaultStyleFunction(),
      zIndex: 1000
    })
  
    this.active = true
    
    this._dragSegments = []
  
    /**
     * false
     *
     * modified
     * @type {boolean}
     * @private
     */
    this._modified = false
  
    /**
     * changingFeature
     * @type {boolean}
     * @private
     */
    this._changingFeature = true
  
    /**
     * Tracks if the next `singleclick` event should be ignored to prevent
     * accidental deletion right after vertex creation.
     *
     * ignoreNextSingleClick
     * @type {boolean}
     * @private
     */
    this._ignoreNextSingleClick = false
  
    /**
     * shouldAddToVertexs
     * @type {boolean}
     * @private
     */
    this._shouldAddToVertexs = false
  
    /**
     * insertVertices
     * @type {Array}
     * @private
     */
    this._insertVertices = []
  
    /**
     * Keep the mouse-down point
     *
     * downPoint
     * @type {null}
     * @private
     */
    this._downPoint = null
  
    /**
     * Keep the current moved geometry
     *
     * currentMovedGeometry
     * @type {null}
     * @private
     */
    this._currentMovedFeature = null
  
    /**
     * 
     * 
     * 
     * @type {null}
     * @private
     */
    this._oldEditedVertex = null
    
    this._mapViewChangedKey = null
    this._mapRenderKey = null

    this._featuresEventKeys = []
  }
  
  /**
   * featurefeature
   *
   * @property features
   * @returns {Object} feature
   */
  get features () { return this._features }
  set features (value = []) {
    this._features = value

    this._snapSegments = null
    this._rotatableFeature = null
    this._rotatingFeature = null
  
    if (this._vertexFeature) {
      this._overLayer.removeFeature(this._vertexFeature)
      this._vertexFeature = null
    }

    if (this._anchorFeature) {
      this._overLayer.removeFeature(this._anchorFeature)
      this._anchorFeature = null
    }
    
    this._hasMoveableGeometrys =
      value.length === 0 ? false : true
    
    this._dragSegments = []
    this._snappedToVertex = false

    this._featuresEventKeys = []

    this._features.forEach(f => {
      this._featuresEventKeys.push(
        listen(
          f, FeatureEvent.EventType.FEATURE_VISIBLE_CHANGED, 
          this._handleFeatureVisibleChanged, this
        )
      )
    })

    this._initAnthorPointForRotatableGeometry(value)
  }

  _initAnthorPointForRotatableGeometry(features) {
    if (features === null || features === undefined) {
      return
    }

    if (!Array.isArray(features)) {
      return
    }

    if (features.length === 0) {
      return
    }

    const feature = features[0]
    const geometry = feature.geometry

    if (!geometry.rotable) {
      return
    }

    this._rotatableFeature = feature

    const anthor = geometry.getAnchorForRotating(this.map)
    this._anchorFeature = new Feature(new Point(anthor[0], anthor[1]))
    this._anchorFeature.style = [this._anchorPointStyle]

    this._overLayer.addFeature(this._anchorFeature)
  }
  
  /**
   * Handle the mouse events emitted from map.
   *
   * 
   *
   * handleMouseEvent
   * @param browserEvent
   * @returns {boolean}
   */
  handleMouseEvent (browserEvent) {
    if (!(browserEvent instanceof BrowserEvent)) {
      return true
    }

    browserEvent.coordinate = this.coordinateBeyond(browserEvent.coordinate)
    this.lastPointerEvent_ = browserEvent
  
    let handled = false
    if ( browserEvent.type == BrowserEvent.MOUSE_MOVE && 
         !this.handlingDownUpSequence) {
      this._handlePointerMove(browserEvent)
    }

    if (this._vertexFeature && this._deleteCondition(browserEvent)) {
      if (browserEvent.type !== BrowserEvent.SINGLE_CLICK ||
        !this._ignoreNextSingleClick) {
        handled = this.removePoint()
      } else {
        handled = true
      }
    }

    if (browserEvent.type == BrowserEvent.SINGLE_CLICK) {
      this._ignoreNextSingleClick = false
    }
  
    return super.handleMouseEvent(browserEvent) && !handled
  }

  /**
   * Removes the vertex currently being pointed.
   *
   * removePoint
   * @return {boolean} True when a vertex was removed.
   * @api
   */
  removePoint () {
    if (this._lastPointerEvent && 
        this._lastPointerEvent.type != BrowserEvent.MOUSE_DRAG) {
      let evt = this.lastPointerEvent_
      this.willModifyFeatures_(evt)
      this._removeVertex()
      
      this.dispatchEvent(new ModifyEvent(
        ModifyEvent.EventType.MODIFY_END, this.features, evt))
      this._modified = false
      return true
    }
    
    return false
  }
  
  /**
   * compareIndexes
   * @param a
   * @param b
   * @returns {number}
   * @private
   */
  _compareIndexes (a, b) {
    return a.index - b.index
  }
  
  /**
   * willModifyFeatures
   * @param event
   * @private
   */
  _willModifyFeatures (event) {
    if (!this._modified) {
      this._modified = true
      this.dispatchEvent(new ModifyEvent(ModifyEvent.EventType.MODIFY_START,
        this.features, event))
    }
  }
  
  /**
   * Handle poiner mouse down event
   *
   * handleDownEvent
   * @param evt {Event}
   * @private
   */
  _handleDownEvent (evt) {
    this._downPoint = evt.coordinate
    this._dragSegments.length = 0
    this._modified = false
    const coordinate = evt.coordinate
    
    const rotatingFeature = this._rotatingFeature
    if (rotatingFeature) {
      return !!rotatingFeature
    }
    
    const vertexFeature = this._vertexFeature
    if (vertexFeature) {
      const insertVertices = [this._snapSegments]
      this._shouldAddToVertexs = true
      this._insertVertices = insertVertices
  
      return !!this._vertexFeature
    }
    // Move the selected geometry
    else {
      if (!this._hasMoveableGeometrys) {
        return
      }
      
      const filters = this.features.filter(feature => {
        const geometry = feature.geometry
        return feature.display &&
          this.movableGeometrySet.has(geometry.geometryType) &&
          geometry.containsXY(coordinate[0], coordinate[1])
      })
      
      // We get the first filter geometry to move
      // Otherwise, you should reselect
      if (filters.length > 0) {
        this._currentMovedFeature = filters[0]
        return true
      }
      
      return false
    }
  }
  
  /**
   * Handle pointer move event
   *
   * 
   *
   * handlePointerMove
   * @param evt {Event}
   * @private
   */
  _handlePointerMove (evt) {
    this._lastPixel = evt.pixel
    this._handlePointerAtPixel(evt.pixel, evt.map)
  }
  
  /**
   * @param pixel {Array}
   * @param map {map}
   * @private
   */
  _handlePointerAtPixel (pixel, map) {
    this._snapSegments = null
    if (this.features.length === 0) {
      return
    }

    const pixelCoordinate = map.getCoordinateFromPixel(pixel)

    // 
    const rotatableGeometry = this._getRotatableGeometry(
      this.features, pixelCoordinate, 7
    )
    if (rotatableGeometry) {
      return
    }
    
    // 
    const nodes = getCollisionFeaturesByPoint(
      this.features, pixelCoordinate, this._pixelTolerance)

    if (nodes.length > 0) {
      const sortByDistance = (s1, s2) => {
        const d1 = this._pointDistanceToSegment(pixelCoordinate, s1)
        const d2 = this._pointDistanceToSegment(pixelCoordinate, s2)
        return d1 - d2
      }

      // 
      nodes.sort(sortByDistance)
      const node = nodes[0]

      const geometry = node.geometry
      const geometryType = geometry.geometryType
      const closestSegment = node.segment

      // 
      let vertex = this._closestOnSegment(pixelCoordinate, node)
      const vertexPixel = map.getPixelFromCoordinate(vertex)
      
      // 
      let dist = distance(pixel, vertexPixel)
      if (dist <= this._pixelTolerance) {
        /**
         *  
         */
        this._snapSegments = node
        
        let vertexEditable = false
  
        if (geometryType === Geometry.CIRCLE &&
          node.index === Modify.MODIFY_SEGMENT_CIRCLE_CIRCUMFERENCE_INDEX) {
          this._snappedToVertex = true
        } else {
  
          /**
           * 
           */
          
          // 
          if (this._vertexEditableCondition(node)) {
            const pixel1 = map.getPixelFromCoordinate(closestSegment[0])
            const pixel2 = map.getPixelFromCoordinate(closestSegment[1])

            const squaredDist1 = squaredDistance(
              vertexPixel[0], vertexPixel[1], pixel1[0], pixel1[1])
            const squaredDist2 = squaredDistance(
              vertexPixel[0], vertexPixel[1], pixel2[0], pixel2[1])

            dist = Math.sqrt(Math.min(squaredDist1, squaredDist2))
  
            // 
            this._snappedToVertex = dist <= this._pixelTolerance
            if (this._snappedToVertex) {
              vertex = squaredDist1 > squaredDist2 ?
                closestSegment[1] : closestSegment[0]
    
              const coordinateIndex = geometry.getCoordinateIndex(vertex)

              switch(geometryType) {
                case Geometry.POLYGON:
                case Geometry.PARALLELOGRAM:
                case Geometry.ROTATABLE_EXTENT:
                  this._snapSegments.index = coordinateIndex.index
                  this._snapSegments.ringIndex = coordinateIndex.ringIndex
                  break
                case Geometry.MULTI_POLYGON:
                  this._snapSegments.index = coordinateIndex.index
                  this._snapSegments.ringIndex = coordinateIndex.ringIndex
                  this._snapSegments.polygonIndex = coordinateIndex.polygonIndex
                  break
                default:
                  this._snapSegments.index = coordinateIndex
                  break
              }

              this._snapSegments.isVertex = true
              vertexEditable = true
            }
          }

          if (geometryType === Geometry.PARALLELOGRAM ) {
            this._snapSegments.index = node.index
            this._snapSegments.ringIndex = node.ringIndex
          }
        }
  
        // 
        if (this._edgeEditableCondition(node) || vertexEditable) {
          // draw the snapping point
          this._createOrUpdateVertexFeature(vertex)
        } else {
          this._snapSegments = null
        }
        
        return
      }
    }
    
    if (this._vertexFeature) {
      this._overLayer.removeFeature(this._vertexFeature)
      this._vertexFeature = null
    }
  }
  
  /**
   * Handle mouse drag event.
   *
   * handleDragEvent
   * @param evt {Event}
   * @private
   */
  _handleDragEvent (evt) {
    this._ignoreNextSingleClick = false
    let vertex = evt.coordinate // 

    // 
    if (this._rotatingFeature) {
      this._handleRotating(evt)
      return
    }

    // 
    if (this._shouldAddToVertexs) {
      const vertexFeature = this._vertexFeature
      const vertexTemp = vertexFeature.geometry.getCoordinates()
    
      const insertVertices = this._insertVertices
      for (let j = insertVertices.length - 1; j >= 0; --j) {
        this._insertVertex(insertVertices[j], vertexTemp)
      }
    
      this._shouldAddToVertexs = false
    }
    
    const dragSegments = this._dragSegments
    const len = dragSegments.length
    let geometry = null

    // snapsnap
    const closeToVertex = this._calculateCloseToVertexOfOhters(evt.pixel)
    if (closeToVertex != null) {
      vertex = closeToVertex
    }

    /**
     * 1
     * 2
     */
  
    // / move the edge of selected geometry
    if (len !== 0) {
      this._willModifyFeatures(evt)
      
      for (let i = 0; i < len; ++i) {
        const dragSegment = dragSegments[i]
        const segmentData = dragSegment[0]
        const index = dragSegment.index

        let { depth, segment } = segmentData
        let coordinates = null
        geometry = segmentData.geometry
        
        switch (geometry.geometryType) {
          case Geometry.POINT:
            coordinates = vertex
            segment[0] = segment[1] = vertex
            break
          case Geometry.MULTI_POINT:
            coordinates = geometry.getCoordinates()
            coordinates[segmentData.index] = vertex
            segment[0] = segment[1] = vertex
            break
          case Geometry.LINE:
            coordinates = geometry.getCoordinates()
            coordinates[segmentData.index] = vertex
            break
          case Geometry.MULTI_LINE:
            coordinates = geometry.getCoordinates()
            coordinates[depth[0]][segmentData.index + index] = vertex
            segment[index] = vertex
            break
          case Geometry.POLYGON:
            coordinates = geometry.getCoordinates()
            coordinates[segmentData.ringIndex][segmentData.index] = vertex
            if (segmentData.index === 0) {
              coordinates[segmentData.ringIndex]
              [coordinates[segmentData.ringIndex].length - 1] = vertex
            }
            break
          case Geometry.PARALLELOGRAM:
            // 
            coordinates = Parallelogram.updateCoordinatesForModification(
              geometry, vertex, this._oldEditedVertex, dragSegment)
            this._oldEditedVertex = vertex
            break
          case Geometry.ROTATABLE_EXTENT:
            // 
            coordinates = RotatableExtent.updateCoordinatesForModification(
              geometry, vertex, this._oldEditedVertex, dragSegment)

            const withoutView = coordinates[0].find(coor => 
              !super.checkIfCoordinateWithInMapView(coor)
            )

            if (withoutView) {
              return
            }
            
            this._oldEditedVertex = vertex
            break
          case Geometry.EXTENT:
            coordinates = ExtentUtil.updateExtent(geometry, vertex, dragSegment)
            break
          case Geometry.MULTI_POLYGON:
            coordinates = geometry.getCoordinates()
            coordinates[segmentData.polygonIndex]
                       [segmentData.ringIndex]
                       [segmentData.index] = vertex
                       
            if (segmentData.index === 0) {
              coordinates[segmentData.polygonIndex]
                         [segmentData.ringIndex]
                         [coordinates[segmentData.polygonIndex]
                         [segmentData.ringIndex].length - 1] = vertex
            }
            break
          case Geometry.CIRCLE:
            segment[0] = segment[1] = vertex
            if (segmentData.index === Modify.MODIFY_SEGMENT_CIRCLE_CENTER_INDEX) {
              this._changingFeature = true
              geometry.setCenter(vertex)
              this._changingFeature = false
            } else { // We're dragging the circle's circumference:
              this._changingFeature = true
              geometry.setRadius(distance(geometry.getCenter(), vertex))
              this._changingFeature = false
            }
            break
          default:
          // pass
        }

        if (coordinates) {
          this._setGeometryCoordinates(geometry, coordinates)
        }
      }
      
      this._createOrUpdateVertexFeature(vertex)
    }
    //  move the whole selected geometry
    else if (this._currentMovedFeature) {
      const downPoint = this._downPoint
      const dx = vertex[0] - downPoint[0]
      const dy = vertex[1] - downPoint[1]

      geometry = this._currentMovedFeature.geometry
      geometry.move(dx, dy, {
        beyond: {
          xmin: 0,
          ymin: 0,
          xmax: this.map.view.dataExtent[2],
          ymax: this.map.view.dataExtent[3]
        }
      })

      // this.dispatchEvent(
      //   new ModifyEvent( 
      //     ModifyEvent.EventType.MODIFY_END, 
      //     this._currentMovedFeature.clone(), 
      //     evt
      //   )
      // )

      this._modified = true
      
      this.changed()
      this._downPoint = vertex
    }

    // 
    this._hideAnchorFeature(geometry)
  }
  
  /**
   * handleUpEvent
   * @param evt {Event}
   * @returns {boolean}
   * @private
   */
  _handleUpEvent (evt) {
    let modifiedFeature = null
    if (this._dragSegments.length > 0) {
      modifiedFeature = this._dragSegments[0][0].feature.clone()
    }

    this._dragSegments.length = 0
    this._shouldAddToVertexs = false
    this._insertVertices = []
  
    // 
    if (this._vertexFeature) {
      this._overLayer.removeFeature(this._vertexFeature)
      this._vertexFeature = null
    }

    // 
    if (this._rotatingFeature) {
      // 
      this._rotatingFeature = null
      // 
      this._lastRotatablePoint = null
    }

    // 
    if(this._rotatableLine) {
      this._overLayer.removeFeature(this._rotatableLine)
      this._rotatableLine = null
    }

    // 
    if (this._anchorFeature) {
      if (!this._anchorFeature.display) {
        this._anchorFeature.display = true

        const anthor = this._rotatableFeature.geometry.getAnchorForRotating(this.map)
        this._anchorFeature.geometry.setCoordinates(anthor)
      }
    }
    
    if (this._modified) {
      this._modified = false

      if (modifiedFeature === null) {
        modifiedFeature = this._currentMovedFeature.clone()
      }

      this.dispatchEvent(
        new ModifyEvent(ModifyEvent.EventType.MODIFY_END, modifiedFeature, evt))
    }

    this._oldEditedVertex = null
    this._downPoint = null
    this._currentMovedFeature = null
    
    return false
  }
  
  /**
   * 
   * segmentData
   * @param vertex
   * @private
   */
  _insertVertex (segmentData, vertex) {
    let { 
      segment, feature, geometry, 
      depth, isVertex, index 
    } = segmentData

    let coordinates = null
    
    switch (geometry.geometryType) {
      case Geometry.MULTI_LINE:
        coordinates = geometry.getCoordinates()
        coordinates[depth[0]].splice(index + 1, 0, vertex)
        break
      case Geometry.POLYGON:
        coordinates = geometry.getCoordinates()
        if (!isVertex) {
          coordinates[segmentData.ringIndex].splice(index + 1, 0, vertex)
          index = index + 1
        } else {
          // dragIndex = index
        }
        break
      case Geometry.MULTI_POLYGON:
        coordinates = geometry.getCoordinates()
        if (!isVertex) {
          coordinates[segmentData.polygonIndex][segmentData.ringIndex].
            splice(index + 1, 0, vertex)
          index = index + 1
        } else {
          // dragIndex = index
        }
        break
      case Geometry.LINE:
        coordinates = geometry.getCoordinates()
        if (!isVertex) {
          coordinates.splice(index + 1, 0, vertex)
          index = index + 1
        } else {
          // dragIndex = index
        }
        break
      case Geometry.POINT:
        coordinates = geometry.getCoordinates()
        coordinates = vertex
        break 
      case Geometry.EXTENT:
        coordinates = geometry.getCoordinates()
        break
      default:
        break
    }

    if (coordinates != null) {
      this._setGeometryCoordinates(geometry, coordinates)
    }
    
    const newSegmentData = {
      segment: [segment[0], vertex],
      feature,
      geometry,
      depth,
      index,
      ringIndex: segmentData.ringIndex,
      polygonIndex: segmentData.polygonIndex,
      isVertex
    }
    
    this._dragSegments.push([newSegmentData, 1])
    this._ignoreNextSingleClick = true
  }

  _getRotatableGeometry(features, pixelCoordinate, tolarance) {
    const rotatableFeatures = features.filter(
      f => f.geometry.rotable && f.display
    )

    if (rotatableFeatures.length === 0) {
      return null
    }

    if (this._anchorFeature == null) {
      return null
    }

    // 
    if (!this._anchorFeature.display) {
      return null
    }

    const anchor = this._anchorFeature.geometry
    const d = distance([anchor.x, anchor.y], pixelCoordinate)
    if (d <= tolarance) {
      this._rotatingFeature = rotatableFeatures[0]
      return this._rotatingFeature
    }

    this._rotatingFeature = null
    return null
  }

  /**
   * 
   */
  _calculateCloseToVertexOfOhters(pixel) {
    if (!this.snapToOthers) {
      return null
    }

    // featureLayer
    if (!this.featureLayer) {
      return null
    }

    // feature
    const modifiedFeatures = this.features
    const features = this.featureLayer.features.filter( feature => 
      !modifiedFeatures.find( mf => mf.id === feature.id)
    )

    let closeToVertex = null
    const map = this.map

    const pixelCoordinate = map.getCoordinateFromPixel(pixel)

    // feature
    const nodes = getCollisionFeaturesByPoint(
      features, pixelCoordinate, this._pixelTolerance)

    if (nodes.length === 0) {
      return null
    }

    const sortByDistance = (s1, s2) => {
      const d1 = this._pointDistanceToSegment(pixelCoordinate, s1)
      const d2 = this._pointDistanceToSegment(pixelCoordinate, s2)
      return d1 - d2
    }

    // 
    nodes.sort(sortByDistance)
    const node = nodes[0]

    const geometry = node.geometry
    // const geometryType = geometry.geometryType
    const closestSegment = node.segment

    // 
    const vertex = this._closestOnSegment(pixelCoordinate, node)
    const vertexPixel = map.getPixelFromCoordinate(vertex)

    // 
    let dist = distance(pixel, vertexPixel)
    if (dist <= this._pixelTolerance) {
      closeToVertex = vertex

      // 
      const pixel1 = map.getPixelFromCoordinate(closestSegment[0])
      const pixel2 = map.getPixelFromCoordinate(closestSegment[1])

      const squaredDist1 = squaredDistance(
        vertexPixel[0], vertexPixel[1], pixel1[0], pixel1[1])
      const squaredDist2 = squaredDistance(
        vertexPixel[0], vertexPixel[1], pixel2[0], pixel2[1])

      dist = Math.sqrt(Math.min(squaredDist1, squaredDist2))

      // 
      this._snappedToVertex = dist <= this._pixelTolerance
      if (this._snappedToVertex) {
        closeToVertex = 
          squaredDist1 > squaredDist2 ?
          closestSegment[1] : closestSegment[0]
      }
    }

    return closeToVertex
  }
  
  _handleRotating(evt) {
    if (!this._rotatingFeature) {
      return
    }

    if (this._lastRotatablePoint == null) {
      this._lastRotatablePoint = this._downPoint
    }

    if (this._anchorFeature.display) {
      this._anchorFeature.display = false
    }

    // 
    if (this._vertexFeature) {
      this._overLayer.removeFeature(this._vertexFeature)
      this._vertexFeature = null
    }

    const dragPoint = evt.coordinate
    const downPoint = this._lastRotatablePoint

    const rotatableGeometry = this._rotatingFeature.geometry
    const flatInteriorPoint = rotatableGeometry.getFlatInteriorPoint()

    const lineCoordinates = [dragPoint, flatInteriorPoint]
    if (this._rotatableLine === null) {
      this._rotatableLine = new Feature(new Line(lineCoordinates))
      this._rotatableLine.style = [this._rotatableLineStyle]
      this._overLayer.addFeature(this._rotatableLine)
    } else {
      this._rotatableLine.geometry.setCoordinates(lineCoordinates)
    }

    const V1 = [downPoint[0] - flatInteriorPoint[0],
                downPoint[1] - flatInteriorPoint[1]]
    const V2 = [dragPoint[0] - flatInteriorPoint[0],
                dragPoint[1] - flatInteriorPoint[1]]

    const angle = getAngleBetweenTwoVectors(V1[0], V1[1], V2[0], V2[1])
    if (angle !== 0) {
      const rotatedGeometry = this._rotatingFeature.geometry
      // 
      const rotatedCoordinates = rotatedGeometry.getRotatedCoordinates(
        angle, flatInteriorPoint)

      // 
      const withoutView = rotatedCoordinates.find(coor =>
        !super.checkIfCoordinateWithInMapView(coor)
      )

      // 
      if (!withoutView) {
        rotatedGeometry.setCoordinates([rotatedCoordinates])
      }
    }

    this._lastRotatablePoint = evt.coordinate
  }

  _hideAnchorFeature(geometry) {
    if (!geometry) {
      return
    }

    // 
    if (geometry.rotable) {
      if (this._anchorFeature) {
        if (this._anchorFeature.display) {
          this._anchorFeature.display = false
        }
      }
    }
  }

  /**
   * 
   */
  _mapViewChanged() {
    if (this._anchorFeature) {
      if (this._anchorFeature.display) {
        const geometry = this._rotatableFeature.geometry
        const anthor = geometry.getAnchorForRotating(this.map)
        this._anchorFeature.geometry.setCoordinates(anthor)
      }
    }
  }

  /**
   * feature
   * @param {*} featureEvent 
   */
  _handleFeatureVisibleChanged(featureEvent) {
    const feature = featureEvent.feature
    if (this._rotatableFeature === null) {
      return
    }

    if (this._rotatableFeature.id !== feature.id) {
      return
    }

    if (feature.display === false) {
      if (this._anchorFeature) {
        if (this._anchorFeature.display) {
          this._anchorFeature.display = false
        }
      }

      if (this._rotatableLine) {
        if (this._rotatableLine.display) {
          this._rotatableLine.display = false
        }
      }
    } else {
      if (this._anchorFeature) {
        if (!this._anchorFeature.display) {
          this._anchorFeature.display = true
        }
      }
    }
  }
  
  /**
   * Ceometry
   *
   * @method setGeometryCoordinates
   * @param geometry {Object}
   * @param coordinates {Array}
   * @private
   */
  _setGeometryCoordinates (geometry, coordinates) {
    if (coordinates) {
      this._changingFeature = true
      geometry.setCoordinates(coordinates)
      this._changingFeature = false
    }
  }
  
  /**
   * @method pointDistanceToSegment
   *
   * coordinates {Array}
   * @param segmentData
   * @returns {number}
   * @private
   */
  _pointDistanceToSegment (coordinates, segmentData) {
    const geometry = segmentData.geometry
    
    if (geometry.geometryType === Geometry.CIRCLE) {
      const circleGeometry = geometry
      
      if (segmentData.index === Modify.MODIFY_SEGMENT_CIRCLE_CIRCUMFERENCE_INDEX) {
        const distanceToCenterSquared =
          squaredDistance(circleGeometry.getCenter(), coordinates)
        const distanceToCircumference =
          Math.sqrt(distanceToCenterSquared) - circleGeometry.getRadius()
        return distanceToCircumference * distanceToCircumference
      }
    }
    
    return squaredDistanceToSegment(coordinates, segmentData.segment)
  }
  
  /**
   * closestOnSegment
   * @param coordinate
   * @param segmentData
   * @returns {*}
   * @private
   */
  _closestOnSegment (coordinate, segmentData) {
    return closestOnSegment(coordinate, segmentData.segment)
  }
  
  /**
   * Create or update the vertex feature while snapping a point
   * on the edge of geometry
   *
   * createOrUpdateVertexFeature
   * @param point {Array}
   * @private
   */
  _createOrUpdateVertexFeature (point) {
    let vertexFeature = this._vertexFeature
    if (!vertexFeature) {
      vertexFeature = new Feature(new Point(point[0], point[1]))
      this._vertexFeature = vertexFeature
      this._overLayer.addFeature(vertexFeature)
    } else {
      const geom = vertexFeature.geometry
      geom.update(point[0], point[1])
      this._overLayer.changed()
    }
  }

  /**
   * 
   *
   * @method getDefaultStyleFunction
   * @returns {Function}
   */
  getDefaultStyleFunction () {
    const style = Style.createDefaultEditing()
    return function (feature) {
      return style[feature.geometry.geometryType]
    }
  }
  
  /**
   *
   * mapBrowserEvent
   * @returns {*}
   * @private
   */
  _defaultDeleteCondition (mapBrowserEvent) {
    return noModifierKeys(mapBrowserEvent) &&
           singleClick(mapBrowserEvent)
  }
  
  /**
   * activeactive
   *
   * @property active
   * @type {Function}
   * @returns {*}
   */
  get active () { return this._active }
  set active (isActive) {
    if (!isActive) {
      if (this._vertexFeature) {
        this._overLayer.removeFeature(this._vertexFeature)
        this._vertexFeature = null
      }

      if (this._anchorFeature) {
        this._overLayer.removeFeature(this._anchorFeature)
        this._anchorFeature = null
      }

      if (this._rotatableLine) {
        this._overLayer.removeFeature(this._rotatableLine)
        this._rotatableLine = null
      }

      this._featuresEventKeys.forEach(key => unlistenByKey(key))
    }

    this._active = isActive
  }

  /**
   * map, map
   *
   * @type {Function}
   * @property map
   * @param map {Object} mk.map
   */
  get map () {return this._map}
  set map (map) {
    if (this._mapRenderKey) {
      unlistenByKey(this._mapRenderKey)
      this._mapRenderKey = null
    }

    if (this._mapViewChangedKey) {
      unlistenByKey(this._mapViewChangedKey)
      this._mapViewChangedKey = null
    }
  
    if (map) {
      this._map = map
      this._mapRenderKey = listen(this, EventType.CHANGE, map.render, map)
      this._mapViewChangedKey = listen(
        map, EventType.VIEW_CHANGED, this._mapViewChanged, this)
      
      map.addLayer(this._overLayer)
    }
  }

  /**
   * snapToOthers
   */
  get snapToOthers() { return this._snapToOthers }
  set snapToOthers(value) {
    if (this._snapToOthers !== value) {
      this._snapToOthers = value
    }
  }

  get featureLayer() { return this._featureLayer }
  set featureLayer(value) {
    this._featureLayer = value
  }
}

/**
 * @type {number}
 */
Modify.MODIFY_SEGMENT_CIRCLE_CIRCUMFERENCE_INDEX = 1

/**
 * @type {number}
 */
Modify.MODIFY_SEGMENT_CIRCLE_CENTER_INDEX = 0


