
export default class Log {


  static coordinates(coordinates = [], message = '', ) {
    if (Array.isArray(coordinates)) {
      let str = message
      coordinates.forEach(coord => {
        str += `${coord[0]}, ${coord[1]} `
      })

      console.log(str)
    } else {
      console.warn('Array')
    }
  }


}


