import BaseObject from '../core/baseobject'
import Obj from '../utils/obj'
import {listen, unlistenByKey} from '../core/eventmanager'
import {EventType} from '../meek/eventtype'
import Geometry from '../geometry/geometry'
import FeatureEvent from './featureevent'
import LineStyle from '../style/linestyle'

/**
 * The feature class is intent to represent geographic features,
 * with a geometry ,a style and other attrubutes properties.
 * Feature provides a function named style that can be seted a style you want,
 * otherwise will use the style of feature layer.
 *
 * @class Feature
 * @extends BaseObject
 * @module meek
 * @constructor
 */
export default class Feature extends BaseObject {

  constructor(geometry, attributes = {}, displayText, style) {
    super()

    /**
     *
     * @type {undefined}
     * @private
     */
    this._styleFunction = undefined

    /**
     *
     * @type {null}
     * @private
     */
    this._geometryChangeKey = null


    /**
     *
     */
    this.geometry = geometry

    /**
     * feature
     */
    this.initArribute(attributes)


    /**
     * feature
     *
     */
    this.style = style

    /**
     * feature
     *
     */
    this.displayText = displayText

    /**
     * feature
     *
     * @type {Boolean}
     * @private
     */
    this.display = true

    /**
     * feature
     * @type {Boolean}
     * @private
     */
    this.textDisplay = true

    /**
     * feature
     * @type {Boolean}
     * @private
     */
    this.styleHighLight = false

  }

  /**
   *
   * @private
   */
  _handleGeometryChanged() {
    this.changed()
  }

  /**
   * feature
   *
   * @method initArribute
   * @param attributes
   */
  initArribute(attributes) {
    if (attributes instanceof Map) {
      this._attributesMap = attributes
      return
    }
    
    if (!(attributes instanceof Object)) {
      attributes = {}
    }
    
    this._attributesMap = Obj.objectToMap(attributes)
  }

  /**
   * feature
   *
   * @method get
   * @param property
   * @return {*}
   */
  get(property) {
    if (this._attributesMap.has(property)) {
      return this._attributesMap.get(property)
    }

    return undefined
  }

  /**
   * 
   *
   * @method set
   * @param property
   * @param value
   * @return {Map.<K, V>}
   */
  set(property, value) {
    return this._attributesMap.set(property, value)
  }

  /**
   * 
   *
   * @method delete
   * @param property
   * @return {boolean}
   */
  delete(property) {
    return this._attributesMap.delete(property)
  }

  /**
   * 
   *
   * @method forEachAttribute
   * @param callback
   */
  forEachAttribute(callback) {
    this._attributesMap.forEach(callback)
  }

  /**
   * 
   *
   * @method has
   * @param property
   * @return {Boolean}
   */
  has(property) {
    return this._attributesMap.has(property)
  }

  /**
   * Geometry 
   *
   * @property geometry
   * @return {*}
   */
  get geometry() {
    return this._geometry
  }

  set geometry(value) {
    if (value) {
      this._geometry = value

      if (this._geometryChangeKey) {
        unlistenByKey(this._geometryChangeKey)
      }

      this._geometryChangeKey = listen(value,
        EventType.CHANGE, this._handleGeometryChanged, this)
    }
  }

  /**
   * 
   *
   * @property styleFunction
   * @return {*}
   */
  get styleFunction() {
    return this._styleFunction
  }

  set styleFunction(value) {
    this._styleFunction = value
  }

  /**
   * 
   *
   * @property style
   * @return {*}
   */
  get style() {
    return this._style
  }

  set style(value) {
    this._style = value
  }

  /**
   * feature
   *
   * @property text
   * @return {*}
   */
  get displayText() {
    return this._displayText
  }

  set displayText(value) {
    this._displayText = value
    this.changed()
  }

  /**
   * 
   *
   * @property styleHighLight
   * @returns {*}
   */
  get styleHighLight() {
    return this._styleHighLight
  }

  set styleHighLight(value) {
    if (this._styleHighLight === value) {
      return
    }
    
    this._styleHighLight = value
  
    if (this.style === undefined) {
      return
    }
    
    const white = [255, 255, 255]
    const newStyles = []
    
    for (let i = 0,len = this.style.length; i < len; i++) {
      const s = this.style[i]
      newStyles.push(s.clone())
    }

    // @TODO 
    /**
     * 1._originStyle
     * 
     * 
     * 2.strokefill
     * openlayer
     * 
     */
    if (this._styleHighLight) {
      this.set('_originStyle', this.style)
      let firstStyle = null
      let cloneStyle = null
      
      const geometryType = this.geometry.geometryType
      switch(geometryType) {
        case Geometry.LINE:
          firstStyle = newStyles[0]
          firstStyle.width = firstStyle.width + 1

          cloneStyle = firstStyle.clone()
          cloneStyle.width = cloneStyle.width + 1
          cloneStyle.color = white

          newStyles.unshift(cloneStyle)
          break
        case Geometry.EXTENT:
        case Geometry.POLYGON:
        case Geometry.MULTI_POLYGON:
        case Geometry.PARALLELOGRAM:
        case Geometry.ROTATABLE_EXTENT:
          firstStyle = newStyles[0]
          firstStyle.borderStyle.width = firstStyle.borderStyle.width + 1

          cloneStyle = firstStyle.clone()
          cloneStyle.alpha = 0
          cloneStyle.borderStyle.width = firstStyle.borderStyle.width + 1
          cloneStyle.borderStyle.color = white
          cloneStyle.borderStyle.lineCap = LineStyle.LineCap.ROUND
          cloneStyle.borderStyle.lineJion = LineStyle.LineJion.ROUND

          newStyles.unshift(cloneStyle)
          break
        case Geometry.POINT:
          firstStyle = newStyles[0]
          firstStyle.size = firstStyle.size + 3
          firstStyle.borderStyle.width = firstStyle.borderStyle.width + 1
          break
      }

      const textStyle = newStyles[0].textStyle.clone()
      // textStyle.font = 'bold 12px Arial'
      const oriFont = textStyle.font
      const boldFont = `bold ${oriFont.split(' ')[1]} Arial`
      textStyle.font = boldFont
      
      newStyles[0].textStyle = textStyle

      this.style = newStyles
    } else {
      if (this.get('_originStyle')) {
        this.style = this.get('_originStyle')
        this.delete('_originStyle')
      }
    }
    
    this.changed()
  }


  /**
   * 
   *
   * map
   *
   * @method display
   * @param value
   */
  get display() {
    return this._display
  }

  set display(value) {
    if (this._display !== value) {
      this._display = value
      // 
      this.changed()

      // 
      this.dispatchEvent(
        new FeatureEvent(FeatureEvent.EventType.FEATURE_VISIBLE_CHANGED, this))
    }
  }

  /**
   * 
   *
   * map
   *
   * @method textDisplay
   * @param value
   */
  get textDisplay () { return this._textDisplay }
  set textDisplay (value) {
    if (this._textDisplay !== value) {
      this._textDisplay = value
      // 
      this.changed()
    }
  }

  /**
   * map
   *
   * @method cloneAttributesMap
   * @returns {Map}
   */
  cloneAttributesMap () {
    const newMap = new Map()
    if (this._attributesMap.size !== 0) {
      const entries = this._attributesMap.entries()
      for (let [key, value] of entries) {
        if (key != '_originStyle') {
          newMap.set(key, Obj.deepClone(value))
        }
      }
    }

    return newMap
  }

  /**
   * features 
   *
   * @method clone
   * @returns {Feature}
   */
  clone () {
    let renderStyleArr
    if (this.style) {
      let style
      if (Array.isArray(this.style)) {
        // 
        if (this.style.length > 1) {
          style = this.style[1].clone()
        } else {
          style = this.style[0].clone()
        }
      } else {
        style = this.style.clone()
      }

      renderStyleArr = [style]
    }

    const newFeature = new Feature(this.geometry.clone(),
      this.cloneAttributesMap(), this.displayText, renderStyleArr)

    return newFeature
  }
}




